/* Variables */

// Base color
$blue: #324157;
$light-blue: #3a71a8;
$red: #c03639;
$pink: #ef889d;
$green: #30b08f;
$tiffany: #acdde7;
$yellow: #ea8a00;
$panGreen: #30b08f;

// Sidebar
$sideBarWidth: 210px;
$subMenuBg: #d7d7d7;
$subMenuHover: #c7c7c7;
$subMenuActiveText: #585858;
$menuBg: #e7e7e7;
$menuText: #4a4a4a;
$menuActiveText: #f28907; // Also see settings.sidebarTextTheme

// Login page
$lightGray: #eee;
$darkGray: #889aa4;
$loginBg: #2d3a4b;
$loginCursorColor: #fff;

// The :export directive is the magic sauce for webpack
// https://mattferderer.com/use-sass-variables-in-typescript-and-javascript
:export {
  menuBg: #e7e7e7;
  menuText: #4a4a4a;
  menuActiveText: #f28907;
}
/* Mixins */
@mixin clearfix {
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}









































.emptyGif {
  display: block;
  width: 45%;
  margin: 0 auto;
}

.dashboard-editor-container {
  background-color: #e3e3e3;
  min-height: 100vh;
  padding: 50px 60px 0px;

  .info-role {
    font-size: 12px;
    font-weight: 700;
    color: #333;
    display: block;
  }

  .info-container {
    position: relative;
    margin-left: 190px;
    height: 150px;
    line-height: 200px;

    .display_name {
      font-size: 48px;
      line-height: 48px;
      color: #212121;
      position: absolute;
      top: 25px;
    }
  }
}
